// Utils
import { convertInString } from '../../../../utils/js/сonvert';
import { activeColor } from '../../../../utils/ts/activeColor';
// Styles-module
import styles from './skill.module.scss';

const container = convertInString(styles.container, 'active-background-1');
const skill = convertInString(
  styles.skill,
  ...activeColor('textShadow-2', 'color-3')
);
const star = (active: boolean) =>
  convertInString(styles.stars, active && styles.stars__active);
const childs = convertInString(styles.childs, 'active-borderColor-2');

const style = { container, skill, star, childs };
export default style;
