// Utils
import { convertInString } from '../../../utils/js/сonvert';
import { activeColor } from '../../../utils/ts/activeColor';
// Styles-module
import abilities from './abilities.module.scss';

const container = convertInString(
  abilities.container,
  ...activeColor('borderColor-1', 'boxShadow-1')
);
const item = (value: string) =>
  convertInString(
    abilities[`${value}__item`],
    ...activeColor('background-1', 'boxShadow-2')
  );
const subTitle = (value: string) =>
  convertInString(abilities[`${value}__subtitle`], 'active-color-effect');

const style = { container, item, subTitle };
export default style;
